body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App{
  margin: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Work Sans', sans-serif;
  height: 87vh;
}

.stories{
  -webkit-filter: drop-shadow(0 10px 10px #888);
          filter: drop-shadow(0 10px 10px #888);
}

.left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-right: 10rem;
  -ms-flex-pack: start;
      justify-content: flex-start;
  max-width: 350px
}

p{
  margin: 0
}

code {
  font-family: 'Roboto Mono', monospace;
}

@media only screen and (max-width: 768px){
  .App{
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    height: auto;
  }

  .left{
    max-width: 100%;
    margin: 0;
    margin-bottom: 2rem;
  }
} 



